import uniLoadMore from '../../../components/uni-load-more.vue';
export default {
	 components: {//2注册组件
		uniLoadMore
	},
	data() {
		return {
			navList: [{
				title: "未使用",
				count: 3
			}, {
				title: "已使用",
				count: 2
			}, {
				title: "已过期",
				count: 1
			}],
			navIndex: 0,
			token:'',
			page:'',
			type:0,
			allPages:1,
			page:1,
			contList:[],
			loadingText: '加载中...',
			loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},
	created:function(){ 
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	onReachBottom:function(){
		let _this=this;
		_this.userCoupon();
	},
	mounted:function(){
		var _this = this;
		_this.userCoupon();
	},
	methods: {
		navIndexs(index) {
			var _this = this;
			_this.navIndex = index;
			_this.type = index;
			_this.page = 1;
			_this.contList = []
			_this.userCoupon()
		},
		userCoupon:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
				type:_this.type,
				page:_this.page
			};
			_this.loadingType = 1;
			uni.showNavigationBarLoading()
			_this.$axios(_this.$baseUrl.userCoupon,data).then(res =>{
				if(res.data.status==1){
					if(_this.page!=1){
						if (res.data.result.logs == null ||res.data.result.logs==undefined ||res.data.result.logs=='' ) {//没有数据
						    _this.loadingType = 2;
						    uni.hideNavigationBarLoading();//关闭加载动画
						    return;
						}
					}
					_this.page++
					_this.contList = _this.contList.concat(res.data.result.logs)
					_this.navList[0].count = res.data.result.count1;
					_this.navList[1].count = res.data.result.count2;
					_this.navList[2].count = res.data.result.count3;
					_this.allPages = res.data.result.pages
					console.log(_this.allPages)
					_this.loadingType = 0;//将loadingType归0重置
					uni.hideNavigationBarLoading();//关闭加载动画
				}else{
					_this.loadingType = 2;
					uni.hideNavigationBarLoading();//关闭加载动画
				}
			}).catch(error =>{
				
			})
		},
	}
}
